 */
/**
 * General reset 
 */
html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  scroll-behavior: smooth;
  /**
   * Custom scrollbar
   */
  /**
   * Custom selection color
   */
}
html::-webkit-scrollbar {
  width: 1.6rem;
}
html::-webkit-scrollbar-track {
  background-color: #fff;
}
html::-webkit-scrollbar-thumb {
  background-color: #6139cf;
  outline: none;
  border-radius: 0rem;
}
html ::selection {
  background-color: #0ca1db;
  color: #fff;
}

html, body {
  font-size: 62.5%;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

body {
  font-size: 1.6rem;
}
body main {
  flex: 1;
  margin-top: 120px;
}

/**
 * Bootstrap reset
 */
.tooltip {
  font-size: 16px;
}
.tooltip .tooltip-arrow::before {
  border-right-color: rgba(58, 58, 58, 0.9);
}
.tooltip .tooltip-inner {
  padding: 8px 16px;
  background-color: rgba(58, 58, 58, 0.9);
  border-radius: 4px;
}

img {
  -webkit-user-drag: none;
}
img.px {
  image-rendering: pixelated;
}
img.aa {
  image-rendering: -webkit-optimize-contrast;
  image-rendering: -moz-crisp-edges;
  image-rendering: -o-crisp-edges;
  image-rendering: crisp-edges;
  image-rendering: optimize-contrast;
  -ms-interpolation-mode: nearest-neighbor;
}

a {
  text-decoration: none;
  color: #6139cf;
}

ul {
  list-style: none;
}

/* Animations */
@keyframes floating {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(15px);
  }
  100% {
    transform: translateY(0);
  }
}
/* Helpers */
.up {
  text-transform: uppercase;
}

.greyscale {
  filter: grayscale(100%);
}

[disabled]:hover {
  cursor: not-allowed;
}

/* Buttons */
button {
  all: unset;
}

.navbar {
  position: unset;
  display: unset;
  flex-wrap: unset;
  align-items: unset;
  justify-content: unset;
  padding-top: unset;
  padding-bottom: unset;
}

.form-control,
.btn {
  font-size: 1.6rem;
}

.btn {
  padding: 12px 16px;
}

.form-control:focus {
  box-shadow: none;
}

/**
 * Por convenção os nomes das cores devem começar com
 * dois traços (--), ao estilo das variáveis CSS.
 */
.btn {
  display: inline-block;
  border-radius: 24px;
  padding: 12px 16px;
  border: none;
  transition: 0.2s ease-in-out;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
}

/**
 * Solid button
 */
.btn {
  background-color: #6139cf;
  color: #fff;
  box-shadow: 2px 3px 8px rgba(0, 0, 0, 0.25);
}
.btn:hover {
  background-color: #714ed4;
  box-shadow: 2px 8px 16px rgba(0, 0, 0, 0.3);
}
.btn:active {
  background-color: #8262d9;
}
.btn:focus-visible {
  background-color: #a38be3;
}